.zolo-navigation-wrapper {
    .zolo-navigation-item {
        .zolo-navigation-link {
            text-decoration: none;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .zolo-submenu-arrow {
                --zolo-submenu-arrow-size: 12px;
                border: none;
                background: transparent;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 5px;
                cursor: pointer;
                z-index: 99;
                color: currentColor;
                pointer-events: none;
                svg {
                    color: inherit;
                    fill: currentColor;
                    font-size: inherit;
                    width: var(--zolo-submenu-arrow-size);
                    height: var(--zolo-submenu-arrow-size);
                    display: block;
                }
            }
        }
        &.current-item {
            a {
                color: var(--zoloblocks-brand-color);
            }
        }
        .zolo-navigation-submenu-wrapper {
            position: absolute;
            width: 100%;
            height: auto;
            left: 0;
            top: 100%;
            visibility: hidden;
            height: 0;
            opacity: 0;
            z-index: 999;
            pointer-events: none;
            box-sizing: border-box;
            transform: translateY(10px);
            transition: 0.3s ease-out;
            text-align: -webkit-auto;
            text-align: auto;

            & > .wp-block-zolo-navigation-submenu .zolo-navigation-submenu-wrapper {
                top: 0;
                left: 100%;
                width: 100%;
            }
        }

        &.is-selected .zolo-navigation-submenu-wrapper,
        &.has-child-selected .zolo-navigation-submenu-wrapper {
            visibility: visible;
            opacity: 1;
            pointer-events: all;
            height: auto;
        }
        &.zolo-navigation-submenu-open > .zolo-navigation-submenu-wrapper {
            visibility: visible;
            opacity: 1;
            pointer-events: all;
            height: auto;
            position: relative;
            top: unset;
            left: unset;
        }
        &.has-megamenu {
            position: static;
        }
    }

    &.zolo-navigation-open {
        .zolo-navigation-item {
            .zolo-navigation-link {
                .zolo-submenu-arrow {
                    pointer-events: all;
                }
            }
        }
    }

    &:not(.is-menu-active) {
        .zolo-navigation-item {
            &:hover {
                & > .zolo-navigation-submenu-wrapper {
                    visibility: visible;
                    opacity: 1;
                    pointer-events: all;
                    transform: translateY(0);
                }
            }
        }
    }
}
